.wrapper {
    width:var(--width);
    margin:0 auto;
}

.title {
    text-align:left;
    padding:0;
    margin:0;
    margin-bottom:20px;
}

.list {
    list-style:none;
    padding:0;
    margin:0;
    display:flex;
    flex-wrap:wrap;
    justify-content:center;
}
.item {
    padding:10px;
    background: linear-gradient(170deg, #5fccff,#81b3f6);
    box-shadow:2px 2px 5px rgb(180, 180, 180);
    border-radius:10px;
    margin-right:5px;
    margin-bottom:5px;

}
.item:last-child {
    margin-right:0px;
}